// Creates a printable page for the specific court with a qr code
import { Button } from "@mui/material";
import React, { useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";

const PrintCourtQRCode = (props: any): JSX.Element => {


    
    const [court, setCourt] = useState<any>(undefined);
    const [facility, setFacility] = useState<any>(undefined);

    // court id should be passed in the url, 
    const { courtId } = useParams();


    // Call api to get court information
    const getCourtUrl = "https://capstone3parksapp.azurewebsites.net/court/" + courtId; //
    const getFacilityBaseUrl = "https://capstone3parksapp.azurewebsites.net/facility/"; //

    useEffect(() => {
        console.log('Retreiving from Backend...');
        const requestOptions = {
            method: 'GET',
            headers: {
                'Access-Control-Allow-Credentials':'true',
                'Accept': 'application/json',
                'Access-Control-Allow-Origin':  'https://capstone3parksapp.azurewebsites.net/',
                'Access-Control-Allow-Methods': 'GET',
                'Access-Control-Allow-Headers': 'Accept'
            },
        };
        fetch(getCourtUrl, requestOptions )
            .then(response => response.json())
            .then(data => {
                console.log("URL sent to :", getCourtUrl);
                console.log(data);

                setCourt(data);
                // NOW Retreive facility name and address with data.facility_id
                fetch(getFacilityBaseUrl + data.facility_id, requestOptions )
                .then(response => response.json())
                .then(facilityData => {
                    console.log(facilityData);
                    setFacility(facilityData);
                }) // end of .then where data is retrieved for facility
                .catch(() => {
                    console.log("ERROR Failed to find facility in databse");
                    //navigate(-1); // send user back a page
                })
            }) // end of .then where data is retrieved for court
            .catch(() => {
                console.log("ERROR Failed to find court in databse");
                //navigate(-1); // send user back a page
            })
    }, [])


    const urlToEmbed = "https://wendigoparks.github.io/%23/court/" + courtId

    // get QR code from this endpoint with these instructions:
    /*
         could use window.innerWidth and window.innerHeight to make QR code take up portion of window?

        Call the URL https://api.qrserver.com/v1/create-qr-code/?size=150x150&data=Example to get a QR code with the content "Example":

        use GET and 
        content-type: image/x-icon
        to get the image then somehow put it in a tag?, or just grab img tag that comes in HTML
        OR use their documentation's hint and:
        As you can see, it's easy to embed a QR code in your (X)HTML documents by using the <img> tag. The <img> tag
        <img src="https://api.qrserver.com/v1/create-qr-code/?data=HelloWorld&amp;size=100x100" alt="" title="" />
    */

    const printPage = async() => {
        const printableComponent = document.getElementById('PrintPage');
        const printableElements = printableComponent != null ? printableComponent.innerHTML : null;
        if (printableElements == null) { return; }
        else {
            const orderHtml: string = await '<html><head><title></title></head><body>' + printableElements + '</body></html>'
            const oldPage = await document.body.innerHTML;
            document.body.innerHTML = await orderHtml;
            await window.print();
            document.body.innerHTML = await oldPage;
        }
    }


    return (
        <div>
            <div
                style={{padding:50}}
            >
                <Button 
                    onClick={printPage}
                    variant="contained"
                >
                    print page
                </Button>
            </div>
            <div id="PrintPage" >
            {/* ONLY PRINTS OUT THIS WHOLE COMPONENT BEAUTIFULLY */}
                <div style={{display:'flex', flexDirection:'column', alignItems:'center'}}>
                    <img className={"logoImg"} alt="wendigo" src="/WenDiGo.png" width="400" height="150"/>
                    <h1 style={{color:'black', margin:10}}>Court: {court ? court.name : "UNKNOWN"} </h1>
                    <p style={{fontSize:30, margin:5}}>Facility: {facility ? facility.name : "UNKNOWN"}</p>
                    <p style={{fontSize:30, margin:5}}> Address: {facility ? facility.address : "UNKNOWN"}</p>
                    <p>____________________________________________________________</p>
                    {/*https://www.qrcode-monkey.com/qr-code-api-with-logo/ */}
                    <img 
                        src={`https://api.qrserver.com/v1/create-qr-code/?data=${urlToEmbed}&size=400x400`} 
                        alt="A QR code for the given court" 
                        title="" 
                    />
                    <p>____________________________________________________________</p>
                    <h2>Find parks and make reservations early at:</h2>
                    <h1 style={{color:'blue'}}> https://wendigoparks.github.io </h1>
                </div>
            </div>
        </div>

    )
}


export default PrintCourtQRCode;