
// Search for friends by username and see their reservation schedules. 
import React from "react";
import SearchIcon from '@mui/icons-material/Search';
import { Button, InputBase, TextField, alpha, styled } from "@mui/material";
import UserReservations from "./UserReservations";

const Search = styled('div')(({ theme }) => ({
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: alpha(theme.palette.common.white, 0.15),
    '&:hover': {
        backgroundColor: alpha(theme.palette.common.white, 0.25),
    },
    marginRight: theme.spacing(2),
    marginLeft: 0,
    width: '100%',
    [theme.breakpoints.up('sm')]: {
        marginLeft: theme.spacing(3),
        width: 'auto',
    },
}));

const SearchIconWrapper = styled('div')(({ theme }) => ({
    padding: theme.spacing(0, 2),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
}));

const StyledInputBase = styled(InputBase)(({ theme }) => ({
    color: 'inherit',
    '& .MuiInputBase-input': {
        padding: theme.spacing(1, 1, 1, 0),
        // vertical padding + font size from searchIcon
        paddingLeft: `calc(1em + ${theme.spacing(4)})`,
        transition: theme.transitions.create('width'),
        width: '100%',
        [theme.breakpoints.up('md')]: {
            width: '20ch',
        },
    },
}));


const Friends = () => {
    
    const [searchQuery, setSearchQuery] = React.useState("");

    
    

    return (
        <div>
            <div className="middleBox">
                <div className="SearchBar" style={{display:'flex', flexDirection:'column', justifyContent:'center', alignItems:'center'}}>
                        <Search style={{width:'30%', backgroundColor:'lightGreen'}}>
                            <div onClick={() => alert("You searched!")}>
                            <SearchIconWrapper>
                                <SearchIcon />
                            </SearchIconWrapper>
                            </div>
                            <StyledInputBase
                                onChange={e => setSearchQuery(e.target.value)}
                                placeholder="Search…"
                            />
                        </Search>
                        <UserReservations username={searchQuery ? searchQuery.trim() : ""} />
                    </div>
            </div>
        </div>
    )
}

export default Friends;