import { Button } from "@mui/material";
import React, { useEffect } from "react"
import { useNavigate } from "react-router-dom";


// Display one Reservation
const Reservation = (props: any) => {

    
    //const [facility, setFacility] = React.useState<any>({});
    //const [court, setCourt] = React.useState<any>({});
    const [facilityType, setFacilityType] = React.useState("");
    const [facilityAddress, setFacilityAddress] = React.useState("");
    const [courtName, setCourtName] = React.useState("");
    const [parkId, setParkId] = React.useState("");


    // Call api to get court information
    const getCourtUrl = "https://capstone3parksapp.azurewebsites.net/court/" + props.reservation.court_id; //
    const getFacilityBaseUrl = "https://capstone3parksapp.azurewebsites.net/facility/"; //

    useEffect(() => {
        console.log('Retreiving from Backend...');
        const requestOptions = {
            method: 'GET',
            headers: {
                'Access-Control-Allow-Credentials':'true',
                'Accept': 'application/json',
                'Access-Control-Allow-Origin':  'https://capstone3parksapp.azurewebsites.net/',
                'Access-Control-Allow-Methods': 'GET',
                'Access-Control-Allow-Headers': 'Accept'
            },
        };
        fetch(getCourtUrl, requestOptions )
            .then(response => response.json())
            .then(data => {
                console.log("URL sent to :", getCourtUrl);
                console.log(data);

                setCourtName(data.name);
                //setCourt(data);
                // NOW Retreive facility name and address with data.facility_id
                fetch(getFacilityBaseUrl + data.facility_id, requestOptions )
                .then(response => response.json())
                .then(facilityData => {
                    console.log(facilityData);
                    setFacilityAddress(facilityData.address);
                    setFacilityType(facilityData.type);
                    setParkId(facilityData.park_id);

                    //setFacility(facilityData);
                }) // end of .then where data is retrieved for facility
                .catch(() => {
                    console.log("ERROR Failed to find facility in databse");
                    //navigate(-1); // send user back a page
                })

            }) // end of .then where data is retrieved for court
            .catch(() => {
                console.log("ERROR Failed to find court in databse");
                //navigate(-1); // send user back a page
            })
    }, [])







    // reservation given in props.reservation
    /*
        Reservation table looks like:
        id VARCHAR(36) PRIMARY KEY,
        date DATE NOT NULL,
        start_time TIME NOT NULL,
        end_time TIME NOT NULL,
        user_id INTEGER NOT NULL,
        court_id VARCHAR(36) NOT NULL,
        group_size TINYINT,
        number_can_join TINYINT default 0,
        number_can_join_total TINYINT default 0,
    */

    const getTimeFrom = (): string => {
        // convert military time props with seconds into nice hours and minutes with AM PM. 
        let hours =  parseInt(props.reservation.start_time.split(":")[0]);
        let minutes = parseInt(props.reservation.start_time.split(":")[1]);
        let suffix = "";
        if (hours <= 12) {
            suffix = " AM";
        } else {
            suffix = " PM";
        }
        if (hours > 12) {
            hours -= 12;
        }
        return "" + hours + ":" + minutes + suffix;
    }


    const getTimeUntil = (): string => {
        // convert military time props with seconds into nice hours and minutes with AM PM. 
        let hours =  parseInt(props.reservation.end_time.split(":")[0]);
        let minutes = parseInt(props.reservation.end_time.split(":")[1]);
        let suffix = "";
        if (hours <= 12) {
            suffix = " AM";
        } else {
            suffix = " PM";
        }
        if (hours > 12) {
            hours -= 12;
        }
        return "" + hours + ":" + minutes + suffix;
    }

    
    const navigate = useNavigate();


    return (
        <Button
            onClick={() => navigate(process.env.PUBLIC_URL + '/park/' + parkId)}
        >
            <div style={{textTransform:'none', margin:2, border:'3px solid black', display:'flex', justifyContent:'space-evenly', width: 600 }}>
                <div style={{display:'flex', flexDirection:'column', justifyContent:'center', alignItems:'center', alignContent:'center', width:120, borderRight:'1px solid black'}}>
                    <p style={{margin:5, padding:0}}>From: {getTimeFrom()}</p>
                    <p style={{margin: 5,  padding:0}}>Until: {getTimeUntil()}</p>
                </div>
                <div style={{display:'flex', flexDirection:'column', justifyContent:'center', alignItems:'center', alignContent:'center', width:120, borderRight:'1px solid black'}}>
                    <p style={{margin:5, padding:0}}>Facility:</p>
                    <p style={{margin: 5,  padding:0}}>{facilityType}</p>
                </div>
                <div style={{display:'flex', flexDirection:'column', justifyContent:'center', alignItems:'center', alignContent:'center', width:120, borderRight:'1px solid black'}}>
                    <p style={{margin:5, padding:0}}>Court:</p>
                    <p style={{margin: 5,  padding:0}}>{courtName}</p>
                </div>
                <div style={{display:'flex', flexDirection:'column', justifyContent:'center', alignItems:'center', alignContent:'center', width:120, borderRight:'1px solid black'}}>
                    <p style={{margin:5, padding:0}}>Address:</p>
                    <p style={{margin: 5,  padding:0}}>{facilityAddress}</p>
                </div>
                <div style={{display:'flex', flexDirection:'column', justifyContent:'center', alignItems:'center', alignContent:'center', width:120}}>
                    <p style={{margin:5, padding:0}}>Group Size:</p>
                    <p style={{margin: 5,  padding:0}}>{props.reservation.group_size}</p>
                </div>
            </div>
        </Button>
        
    )
}

export default Reservation