

import React, { useEffect, useState } from "react";
import Reservation from "./Reservation";
import {DatePicker, LocalizationProvider} from "@mui/x-date-pickers";
import {AdapterDayjs} from "@mui/x-date-pickers/AdapterDayjs";
import dayjs, {Dayjs} from "dayjs";

const UserReservations = (props: any): JSX.Element => {

    // props.username will define the username for the user whose reservations we are seeing.
    // TODO -> if props.me is true, hit the me endpoint, else use props.username to hit that endpoint

    // the url to get the user's reservations
    const getUserReservationsUrl = "https://capstone3parksapp.azurewebsites.net/reservation/user/me?date_str="// 
    const getOtherUserReservationsUrl = "https://capstone3parksapp.azurewebsites.net/reservation/user/"// + props.username;

    // This will hold all of the reservations retreived
    const [reservations, setReservations] = React.useState<any[]>([]);
    const [date, setDate] = useState<Dayjs|null>(dayjs(new Date()));

    // First get all reservations for the user from database
    // This will run once on component mounting
    useEffect(() => {
        if (props.me) {
            console.log('Retreiving from Backend...');
            let newParksArray: any[] = [];
            // check if a search query has been supplied in the url, if it has call
            // backend to perfrom the search, if it hasn't, retreive all parks. 
            let dateString = date? date.toDate().toISOString().substring(0, date.toDate().toISOString().indexOf("T")) : "";
            console.log(dateString);
            let userCompleteUrl = ("https://capstone3parksapp.azurewebsites.net/reservation/user/me?date_str=" + dateString);
            fetch(userCompleteUrl, {
                method: 'GET', 
                headers: { 'Access-Control-Allow-Credentials':'true',
                    'Accept': 'application/json',
                    'Access-Control-Allow-Origin':  'https://capstone3parksapp.azurewebsites.net/',
                    'Access-Control-Allow-Methods': 'GET',
                    'Access-Control-Allow-Headers': 'Accept',
                    'authjwt_cookie_samesite': 'none'},
                credentials: 'include'
            } )
            .then(response => response.json())
            .then(data => {
                console.log("Retrived the following data:\n");
                console.log(data);

                setReservations(data);
            }) // end of .then where data is retrieved for park
            .catch(() => {
                console.log("ERROR Failed to retrieve info");
            })
        } else if (props.username) {
            console.log('Retreiving from Backend...');
            let newParksArray: any[] = [];
            // check if a search query has been supplied in the url, if it has call
            // backend to perfrom the search, if it hasn't, retreive all parks. 
            let dateString = date? date.toDate().toISOString().substring(0, date.toDate().toISOString().indexOf("T")) : "";

            console.log(dateString);
            let otherCompleteUrl = ("https://capstone3parksapp.azurewebsites.net/reservation/user/" + props.username + 
                (date ? "?date_str=" + date.toDate().toISOString().substring(0, date.toDate().toISOString().indexOf("T")) : ""));
            fetch(otherCompleteUrl, {
                method: 'GET', 
                headers: { 'Access-Control-Allow-Credentials':'true',
                    'Accept': 'application/json',
                    'Access-Control-Allow-Origin':  'https://capstone3parksapp.azurewebsites.net/',
                    'Access-Control-Allow-Methods': 'GET',
                    'Access-Control-Allow-Headers': 'Accept',
                    'authjwt_cookie_samesite': 'none'},
                credentials: 'include'
            } )
            .then(response => response.json())
            .then(data => {
                console.log("Retrived the following data:\n");
                console.log(data);

                setReservations(data);
            }) // end of .then where data is retrieved for park
            .catch(() => {
                console.log("ERROR Failed to retrieve info");
            })
        }
      }, [props.username, props.me, date])

    return (
        <div>
            <LocalizationProvider dateAdapter={AdapterDayjs}>
                <DatePicker sx={{minWidth: 150,  marginLeft: 2, marginTop: 3  }} label="Date"
                            onChange={(val) => setDate(val)} value={date}
                />
            </LocalizationProvider>
            {
                reservations[0] ? 
                <div>
                    {
                        reservations.map(reservation => (
                            <Reservation reservation={reservation} />
                        ))
                    }
                </div>
                :
                <div>
                    <h1> No Reservations Found </h1>
                </div>
            }
            

        </div>
    )
 }

export default UserReservations;