// Page to display one individual facility and its schedule
import React, {useEffect, useState} from "react";
import {facilityToIcon, textArrayToSymbolArray} from './FacilityIcons';
import SportsTennisOutlinedIcon from "@mui/icons-material/SportsTennisOutlined";
import {Grid, IconButton, Select, Stack} from "@mui/material";
import Paper from "@mui/material/Paper";
import './Facility.css'
import MenuItem from "@mui/material/MenuItem";
import Button from "@mui/material/Button";
import FormControl from "@mui/material/FormControl";
import InputLabel from "@mui/material/InputLabel";
import dayjs, {Dayjs} from "dayjs";
import {DatePicker, LocalizationProvider} from "@mui/x-date-pickers";
import {AdapterDayjs} from "@mui/x-date-pickers/AdapterDayjs";
import { Link } from "react-router-dom";
import { userIsAdmin } from "./Authentication";

const Facility = (props: any): JSX.Element => {

    const [startHour, setStartHour] = useState<number| string>(0);
    const [startMinute, setStartMinute] = useState<number| string>(0);
    const [endHour, setEndHour] = useState<number| string>(0);
    const [endMinute, setEndMinute] = useState<number| string>(0);
    const [amPM, setAMPM] = useState(false) //false for am, true for pm
    const [court, setCourt] = useState<number| string>(1);
    const [participants, setParticipants] = useState<number| string>(1);
    const [date, setDate] = useState<Dayjs|null>(dayjs(new Date()));
    const [res, setRes] = useState<any[]>([]);

    // needs to be passed props.facility and need to define facility opitons and json structure
    // ie example structure:
    /*
    {
        "id":11
        "category":"Tennis",
        "location":"101 main street",
        "imageURL":"https://mastersystemscourts.com/img/master-systems-courts-tennis-court-1-991x605l.jpg",
        "parkID":"1",

    }
    or how I currently have them:
    {
        "id":1,
        "parkID":1,             // ParkID should just be the park that a specific facility belongs to
        "category":"tennis",
        "name":"Caboose Park Tennis Courts",
        "openTime":"7:00am",
        "closeTime":"10:00pm",  // Should be able to retrieve calender for facility via id and parkid which should be unique.
    }

    */

    console.log("Facility: ", props.facility);

    const getResUrl = "https://capstone3parksapp.azurewebsites.net/reservation/facility/" + props.facility.id; //

    useEffect(() => {
        console.log('Retreiving from Backend...');
        const requestOptions = {
            method: 'GET',
            headers: {
                'Access-Control-Allow-Credentials':'true',
                'Accept': 'application/json',
                'Access-Control-Allow-Origin':  'https://capstone3parksapp.azurewebsites.net/',
                'Access-Control-Allow-Methods': 'GET',
                'Access-Control-Allow-Headers': 'Accept'
            },
        };
        fetch(getResUrl, requestOptions )
            .then(response => response.json())
            .then(data => {
                console.log("Retrived the following data:\n");
                console.log("Res: ", data);
                if(typeof(data.detail)=="undefined") {
                    setRes(data);
                }

            }) // end of .then where data is retrieved for park
            .catch(() => {
                console.log("ERROR Failed to retrieve info for facility with id:" + props.facility.id);
                //navigate(-1); // send user back a page
            })
    }, [props.facility.id])

    const addResEndpoint = "https://capstone3parksapp.azurewebsites.net/reservation/"; // endpoint to add park endpoint

    function handleSubmit() {
        let i1 = startHour as number
        let i2 = endHour as number
        if(amPM){
            i1 =- 12
            i2 =- 12
        }

        if(startHour>endHour||(startHour==endHour&&startMinute>endMinute)){
            alert("End time cannot be after start time!")
        }
        else if(!isOpen(i1, startMinute as number/15) || !isOpen(i2, endMinute as number/15)){
            alert("Facility is closed during this time.")
        }
        else{
            //Get reservations for court
            //Ensure there are no conflicts
            // Make sure necessary park data is provided IE
            // name and address, set everything else to defaults if not
            // provided, and then try to send this new park to the backend as a new park.
            // For now assume park name and address are only required attributes
            if (true) {
                // build park JSON object and send it to backend endpoint:
                const date_str = date?.toDate().toISOString()
                const reservationJson = {
                    date: date_str?.substring(0, date_str.indexOf("T")),
                    start_time: startHour+":"+startMinute,
                    end_time: endHour+":"+endMinute,
                    group_size: participants,
                    court_id: props.facility.courts[court as number -1].id
                }

                console.log(reservationJson);
                // Using Fetch API
                fetch(addResEndpoint, {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json',
                        'Access-Control-Allow-Origin':  'https://capstone3parksapp.azurewebsites.net',
                        'Access-Control-Allow-Methods': 'POST',
                    },
                    body: JSON.stringify(reservationJson),
                    credentials: 'include'
                })
                    .then((response) => {
                        if (!response.ok) {throw new Error(String(response.status)); }
                        else {return response.json(); } })
                    .then((data) => {
                        console.log(data);
                        // successfully posted! clear fields and alert user
                        alert("Congratulations, the reservation has been successfully added!");
                    })
                    .catch((err) => {
                        console.log(err.message);
                        alert("Unable to add reservation to the database")
                    });

            } else {
                alert("Invalid reservation.")
            }
        }
    }

    function toTitleCase(s: string) {
        if (!s) return "";
        s = s.toLowerCase()
            .split(' ')
            .map((s) => s.charAt(0).toUpperCase() + s.substring(1))
            .join(' ');
        return s;
    }

    function getResAmount(i: number, j: number) {
        if(amPM){
            i+=12
        }
        let total = 0
        //console.log("R: ", res)
        let date_str = date?.toDate().toISOString()
        date_str = date_str?.substring(0, date_str.indexOf("T"))
        res.forEach( r => {
            //console.log(r.court_id, " vs ", props.facility.courts[court as number - 1].id)
            if(r.date==date_str && r.court_id === props.facility.courts[court as number - 1].id){ //Check if res matches currently displayed court and date
                let sa = r.start_time.split(":")
                let ea = r.end_time.split(":")

                let sh = sa[0] as number
                let sm = sa[1] as number

                let eh = ea[0] as number
                let em = ea[1] as number

                //console.log(sh, sm, eh, em)

                if((sh<=i&&eh>=i) &&!(i==sh&&(sm>j*15)||(i==eh&&(em<=j*15)))){
                    total += r.group_size
                }
            }
        });
        return total;
    }

    function isOpen(i: number, j: number) {
        if (amPM) {
            i += 12
        }
        let h = dayHours()
        if(typeof(h)!="undefined"){
            let hs = h.split("-");
            let sh = hs[0].split(":")[0]
            let sm = hs[0].split(":")[1]
            let eh = hs[1].split(":")[0]
            let em = hs[1].split(":")[1]
            if ((sh <= i && eh >= i) && !(i == sh && (sm > j * 15) || (i == eh && (em <= j * 15)))) {
                return true;
            }
        }
        return false;
    }

    function dayHours() {
        let weekday = date?.toDate().getDay()
        console.log(weekday)
        let h;
        if(weekday==0){
            h = props.facility.sunday_hours
        }
        else if(weekday==1){
            h = props.facility.monday_hours
        }
        else if(weekday==2){
            h = props.facility.tuesday_hours
        }
        else if(weekday==3){
            h = props.facility.wednesday_hours
        }
        else if(weekday==4){
            h = props.facility.thursday_hours
        }
        else if(weekday==5){
            h = props.facility.friday_hours
        }
        else if(weekday==6){
            h = props.facility.saturday_hours
        }
        return h
    }

    return (
        <div className="Facility" style={{backgroundColor:'#AADE9EFF', }}>
            <span> 
                    <Grid container spacing={1} >
                        <Grid item xs={12}>
                            {typeof(props.facility.courts)!='undefined'&&props.facility.courts[court as number -1].reservable &&
                                <Paper sx={{
                                    paddingTop: 2,
                                    backgroundColor: "#d0e0d1"
                                }}
                                >
                                    <LocalizationProvider dateAdapter={AdapterDayjs}>
                                        <DatePicker sx={{minWidth: 150, marginLeft: 2, marginTop: 3}} label="Date"
                                                    onChange={(val) => setDate(val)} value={date}
                                        />
                                    </LocalizationProvider>
                                    <FormControl variant="filled" sx={{m: 1, maxWidth: 170}}>
                                        <InputLabel>Court</InputLabel>
                                        <Select value={court} onChange={e => setCourt(e.target.value)} sx={{
                                            m: 2
                                        }}>
                                            {Array(typeof (props.facility.courts) != 'undefined' ? props.facility.courts.length : 1).fill(true).map((_, i) =>
                                                <MenuItem value={i + 1} key={"c" + i}>{i + 1}</MenuItem>
                                            )}
                                        </Select>
                                    </FormControl>
                                    <FormControl variant="filled" sx={{m: 1, maxWidth: 170}}>
                                        <InputLabel>Start Hour</InputLabel>
                                        <Select value={startHour} onChange={e => setStartHour(e.target.value)} sx={{
                                            m: 2
                                        }}>
                                            {
                                                Array(24).fill(true).map((_, i) =>
                                                    <MenuItem value={i} key={"ss" + i}>{i}</MenuItem>
                                                )
                                            }
                                        </Select>
                                    </FormControl>
                                    <FormControl variant="filled" sx={{m: 1, maxWidth: 170}}>
                                        <InputLabel>Minute</InputLabel>
                                        <Select value={startMinute} onChange={e => setStartMinute(e.target.value)} sx={{
                                            m: 2,
                                            marginLeft: 0
                                        }}>
                                            <MenuItem value={0}>0</MenuItem>
                                            <MenuItem value={15}>15</MenuItem>
                                            <MenuItem value={30}>30</MenuItem>
                                            <MenuItem value={45}>45</MenuItem>
                                        </Select>
                                    </FormControl>
                                    <FormControl variant="filled" sx={{m: 1, maxWidth: 170}}>
                                        <InputLabel>End Hour</InputLabel>
                                        <Select value={endHour} onChange={e => setEndHour(e.target.value)} sx={{
                                            m: 2
                                        }}>
                                            {
                                                Array(24).fill(true).map((_, i) =>
                                                    <MenuItem value={i} key={"ss" + i}>{i}</MenuItem>
                                                )
                                            }
                                        </Select>
                                    </FormControl>
                                    <FormControl variant="filled" sx={{m: 1, maxWidth: 170}}>
                                        <InputLabel>Minute</InputLabel>
                                        <Select value={endMinute} onChange={e => setEndMinute(e.target.value)} sx={{
                                            m: 2,
                                            marginLeft: 0
                                        }}>
                                            <MenuItem value={0}>0</MenuItem>
                                            <MenuItem value={15}>15</MenuItem>
                                            <MenuItem value={30}>30</MenuItem>
                                            <MenuItem value={45}>45</MenuItem>
                                        </Select>
                                    </FormControl>
                                    <FormControl variant="filled" sx={{m: 1, maxWidth: 170}}>
                                        <InputLabel>Participants</InputLabel>
                                        <Select value={participants} onChange={e => setParticipants(e.target.value)}
                                                sx={{
                                                    m: 2
                                                }}>
                                            {Array(typeof (props.facility.courts) != 'undefined' ? props.facility.capacity : 4).fill(true).map((_, i) =>
                                                <MenuItem value={i + 1} key={"mi" + i}>{i + 1}</MenuItem>
                                            )}
                                        </Select>
                                    </FormControl>
                                    <Button onClick={handleSubmit} variant="contained" sx={{
                                        p: 2,
                                        marginTop: 3
                                    }}>
                                        Submit Reservation
                                    </Button>
                                </Paper>
                            }
                        </Grid>
                        <Grid container wrap='nowrap' columns={2}>
                        <Grid item xs={"auto"} sx={{mx: 'auto', my: 1}}>
                            <Paper
                                sx={{
                                    height: 250,
                                    width: 175,
                                    marginLeft: 2
                                }}
                            >
                                <span className={"parkCategory"}>{toTitleCase(props.facility.category)}</span><br/>
                                <div className="facilityIcon" style={{fontSize:50}}>
                                    {facilityToIcon[props.facility.type]}   
                                </div>
                                <span>{typeof(props.facility.courts)!='undefined'?props.facility.courts[court as number -1].name:""} ({dayHours()})

                                </span>
                                <br></br>
                                {
                                    userIsAdmin() ? 
                                    <Link style={{textDecoration:'none'}} to={process.env.PUBLIC_URL + '/CourtQR/' + 
                                        (typeof(props.facility.courts)!='undefined'?props.facility.courts[court as number -1].id:"")}
                                    >
                                        <Button
                                            variant="outlined"
                                        >
                                            Get QR Code
                                        </Button>
                                    </Link>
                                    :
                                    <div></div>
                                }
                                <FormControl sx={{ m: 1, marginTop: 2, minWidth: 125}}>
                                    <InputLabel >Time Display</InputLabel>
                                    <Select defaultValue={"AM"} style={{height:'50px'}} sx={{
                                        m: 2
                                    }} onChange={e => setAMPM(e.target.value==="AM"? false: true)}>
                                        <MenuItem value={"AM"}>AM</MenuItem>
                                        <MenuItem value={"PM"}>PM</MenuItem>
                                    </Select>
                                </FormControl>
                            </Paper>
                        </Grid>
                      <Grid item xs={"auto"} sx={{margin: 'auto'}}>
                          {typeof (props.facility.courts) != 'undefined' && props.facility.courts[court as number - 1].reservable &&
                              <Grid container justifyContent="center" spacing={0}>
                                  {
                                      Array(12).fill(true).map((_, i) =>
                                          <Stack key={"s" + i} spacing={0}>
                                              {Array(4).fill(true).map((_, j) =>
                                                  <Paper
                                                      sx={{
                                                          paddingLeft: 1,
                                                          paddingRight: 1,
                                                          paddingBottom: 1,
                                                          paddingTop: 1,
                                                          backgroundColor: ((!amPM && startHour <= i && endHour >= i) || (amPM && startHour <= i + 12 && endHour >= i + 12))
                                                          && !((i == startHour && (startMinute > j * 15) || (i == endHour && (endMinute <= j * 15))) ||
                                                              ((amPM && i + 12 == startHour) && (startMinute > j * 15) || ((amPM && i + 12 == endHour) && (endMinute <= j * 15))))
                                                              ? (isOpen(i, j) ? "green" : "black") : (isOpen(i, j) ? "white" : "black")//Add logic here to check if reservation is valid or if slot is full
                                                      }}
                                                  >
                                                      <span
                                                          className={"timeText"}>{amPM ? i + 12 : i}:{(j * 15).toString().padStart(2, '0')}</span>
                                                      <br/>
                                                      <span
                                                          className={"capacityText"}>{res.length != 0 && getResAmount(i, j) != 0 ? "Taken" : "Open"}</span>
                                                  </Paper>
                                              )}
                                          </Stack>
                                      )
                                  }
                              </Grid>
                          }
                          {typeof (props.facility.courts) != 'undefined' && !props.facility.courts[court as number - 1].reservable &&
                              <Paper
                                  sx={{
                                      paddingLeft: 1,
                                      paddingRight: 1,
                                      paddingBottom: 1,
                                      paddingTop: 1,
                                      backgroundColor: "white"}}
                              >
                                  <p>Court unavailable for reservations.</p>
                                  <FormControl variant="filled" sx={{m: 1, maxWidth: 170}}>
                                        <InputLabel>Court</InputLabel>
                                        <Select value={court} onChange={e => setCourt(e.target.value)} sx={{
                                            m: 2
                                        }}>
                                            {Array(typeof (props.facility.courts) != 'undefined' ? props.facility.courts.length : 1).fill(true).map((_, i) =>
                                                <MenuItem value={i + 1} key={"c" + i}>{i + 1}</MenuItem>
                                            )}
                                        </Select>
                                    </FormControl>
                                </Paper>
                          }
                      </Grid>
                    </Grid>
                    </Grid>
            </span>
           
        </div>
    )
}

export default Facility;