// Users are directed here when scanning a QR Code should give them 
// to option to request the NEXT availability for this specific court
// OR direct them to log in to make a reservation. 

import { Button } from "@mui/material";
import React, { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import ParkCard from "./ParkCard";

const CourtQR = (props: any): JSX.Element => {

    // court id should be passed in the url, 
    const { courtId } = useParams();

    // Grab court info from back end using useEffect and display it here
    const [court, setCourt] = useState<any>(undefined);
    const [facility, setFacility] = useState<any>(undefined);
    const [park, setPark] = useState<any>(undefined);

    // Call api to get court information
    const getCourtUrl = "https://capstone3parksapp.azurewebsites.net/court/" + courtId; //
    const getFacilityBaseUrl = "https://capstone3parksapp.azurewebsites.net/facility/"; //
    const getParkBaseUrl = "https://capstone3parksapp.azurewebsites.net/park/"; //

    useEffect(() => {
        console.log('Retreiving from Backend...');
        const requestOptions = {
            method: 'GET',
            headers: {
                'Access-Control-Allow-Credentials':'true',
                'Accept': 'application/json',
                'Access-Control-Allow-Origin':  'https://capstone3parksapp.azurewebsites.net/',
                'Access-Control-Allow-Methods': 'GET',
                'Access-Control-Allow-Headers': 'Accept'
            },
        };
        fetch(getCourtUrl, requestOptions )
            .then(response => response.json())
            .then(data => {
                console.log("URL sent to :", getCourtUrl);
                console.log(data);

                setCourt(data);
                // NOW Retreive facility name and address with data.facility_id
                fetch(getFacilityBaseUrl + data.facility_id, requestOptions )
                .then(response => response.json())
                .then(facilityData => {
                    console.log(facilityData);
                    setFacility(facilityData);
                }) // end of .then where data is retrieved for facility
                .catch(() => {
                    console.log("ERROR Failed to find facility in databse");
                    //navigate(-1); // send user back a page
                })



                // NOW fetch park info
                fetch(getParkBaseUrl + data.park_id, requestOptions )
                .then(response => response.json())
                .then(parkData => {
                    console.log(parkData);
                    setPark(parkData);
                }) // end of .then where data is retrieved for facility
                .catch(() => {
                    console.log("ERROR Failed to find park in databse");
                    //navigate(-1); // send user back a page
                })

            }) // end of .then where data is retrieved for court
            .catch(() => {
                console.log("ERROR Failed to find court in databse");
                //navigate(-1); // send user back a page
            })
    }, [])

    // allows us to direct user to login screen
    const navigate = useNavigate();


    // This component out of ALL Components needs to be optimized for mobile viewing. 
    const getNextAvailability = () => {
        // Ask backend for next availability then tell user when
        // that will be and what random username they will have it under
        alert("You now have the next availability at: 5:00 PM to 6:00 PM under the username: guest user 2")
        console.log(courtId);
    }

    return (
        <div>
            <div className="middleBox">
                <p style={{fontSize:30}}>Thanks for scanning, hop in line as a guest or log in to make a future reservation!</p>
                <div className="QRPageButtons" style={{display:'flex', justifyContent:'space-evenly', padding:20}}>
                    <Button
                        onClick={getNextAvailability}
                        variant="contained"
                    > 
                        Get Next Availability Now
                    </Button>
                    <Button
                        onClick={() => {navigate(process.env.PUBLIC_URL + '/login/' + (park? park.id : ""))}}
                        variant="contained"
                    >
                        Log In or Sign Up For FREE
                    </Button>
                </div>
                <h1 style={{color:'black', margin:10}}>Court: {court ? court.name : "UNKNOWN"} </h1>
                <p style={{fontSize:30, margin:5}}>Facility: {facility ? facility.name : "UNKNOWN"}</p>
                <p style={{fontSize:30, margin:5}}> Address: {facility ? facility.address : "UNKNOWN"}</p>
                <div style={{display:'flex', alignItems:'center', justifyContent:'center'}}>
                    { park? <ParkCard park={park} /> : <div></div> }
                </div>

            </div>
        </div>
    )
}

export default CourtQR;