// Essentially will look the same as the add park page, but it will be pre-populated with the values
// from the existing park, and it will send POST requestst to the endpoint to update a park
// rather than to the one that creates a park. Should also have a button to delete a park on this page.

import React, { useEffect } from "react";
import TextField from '@mui/material/TextField';
import { Button, Paper } from "@mui/material";
import ParkCard from "./ParkCard";
import OutlinedInput from '@mui/material/OutlinedInput';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import ListItemText from '@mui/material/ListItemText';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import Checkbox from '@mui/material/Checkbox';
import { allFacilities,  textArrayToSymbolArray } from "./FacilityIcons";
import AddFacility from "./AddFacility";
import { userIsLoggedIn } from "./Authentication";
import { useParams } from "react-router-dom";

const UpdatePark = (props: any): JSX.Element => {

    // FIRST WE GOT TO PULL INFO FROM THE PARK ID WE WERE GIVEN
    // Instead of coming from props, Ideally just need to pass parkID as query parameter into URL
    // that way links can go to specific parks. 
    const { id } = useParams();

    // Endpoint to send POST request to backend
    const updateParkEndpoint = "https://capstone3parksapp.azurewebsites.net/park/update/"; // endpoint to add park endpoint

    // Default values for non-required fields if an invalid
    // input is given:
    const defaultImageUrl = "https://www.tigard-or.gov/home/showpublishedimage/2810/637698863440870000";
    const defaultDescription = "No Description Given";
    const defaultAmenities = "Amenities Unknown";
    const defaultPhoneNumber = "None";
    const defaultCapacity = "50";
    const defaultState = "N/A";
    const DefaultCounty = "N/A";
    const DefaultLatitude = "0";
    const DefaultLongitude = "0";




    // the url to retreive a specific park from:
    const getParkUrl = "https://capstone3parksapp.azurewebsites.net/park/" + id; // + park_id
    // the url to retreive a specific park from:
    const getParkFacilitiesUrl = "https://capstone3parksapp.azurewebsites.net/facilities_and_courts/" + id; // + park_id
    // the url to retreive a specific park from:
    //const getParkCourtsUrl = "https://capstone3parksapp.azurewebsites.net/courts/" + id; // + facility_id


    // This will hold all of the parks retreived
    const [park, setPark] = React.useState<any>({});
    const [retrievedPark, setRetreivedPark] = React.useState<boolean>(true);

    // Hold park's facilities
    const [facilities, setFacilities] = React.useState<any>([{}]);

    // Hold park's courts
    //const [courts, setCourts] = React.useState<any>([{}]);

    // First get all parks from database
    // This will run once on component mounting
    useEffect(() => {
        let newParksArray: any[] = [];
        const requestOptions = {
            method: 'GET',
            headers: { 
                'Access-Control-Allow-Credentials':'true',
                'Accept': 'application/json',
                'Access-Control-Allow-Origin':  'https://capstone3parksapp.azurewebsites.net/',
                'Access-Control-Allow-Methods': 'GET',
                'Access-Control-Allow-Headers': 'Accept'
            },
        };
        // check if a search query has been supplied in the url, if it has call
        // backend to perfrom the search, if it hasn't, retreive all parks. 
        fetch(getParkUrl, requestOptions )
        .then(response => response.json())
        .then(data => {
            setPark(data);

            setParkName(data.name);
            setDescription(data.description);
            setAddress(data.address);
            setPhoneNumber(data.phone_nr);
            setState(data.state);
            setCounty(data.county);
            setLatitude(data.latitude);
            setLongitude(data.longitude);
            setParkImageURL(data.image_url);
            setCapacity("0");


            // Now retrieve facilities for the park!
            fetch(getParkFacilitiesUrl, requestOptions)
            .then(response => response.json())
            .then(data => {
                // should be receiving facilities here
                // save facilities retreived
                setFacilities(data);
                // for each facility, add it to facilitiesArray and update amenities
                let newAmenitiesArray: any[] = [];
                let newFacilitiesArray: any[] = [];
                data.forEach( (facility: any) => {
                    newAmenitiesArray.push(facility.type);
                    newFacilitiesArray.push(facility);
                });
                setAmenitiesValue(newAmenitiesArray);
                setFacilitiesArray(newFacilitiesArray);

            })


        }) // end of .then where data is retrieved for park
        .catch(() => {
            //navigate(-1); // send user back a page
        })
      }, [id])

    /*
        Parks need to conform to this schema

        name: str
        description: str | None = None
        address: str | None = None
        amenities: str | None = None
        phone_nr: str | None = None
        capacity: int | None = None
        image_url: str | None = None
    */

    const [parkName, setParkName] = React.useState('');
    const [description, setDescription] = React.useState('');
    const [address, setAddress] = React.useState('');
    const [phone_nr, setPhoneNumber] = React.useState('');
    const [state, setState] = React.useState('');
    const [county, setCounty] = React.useState('');
    const [latitude, setLatitude] = React.useState('');
    const [longitude, setLongitude] = React.useState('');
    const [capacity, setCapacity] = React.useState('');
    const [image_url, setParkImageURL] = React.useState('');

    const validateAndSend = () => {
        // Make sure necessary park data is provided IE
        // name and address, set everything else to defaults if not
        // provided, and then try to send this new park to the backend as a new park.
        // For now assume park name and address are only required attributes 
        if (parkName.trim() !== "" && address.trim() !== "") {
            // build park JSON object and send it to backend endpoint:
            const parkJson = {
                "name": parkName,
                "description": description.trim() ? description.trim() : defaultDescription,
                "address": address,
                "amenities": amenitiesValue ? amenitiesValue.join(',') : defaultAmenities,
                "phone_nr": phone_nr.trim() ? phone_nr.trim() : defaultPhoneNumber,
                "capacity": capacity.trim() ? capacity.trim() : defaultCapacity,
                "image_url": image_url.trim() ? image_url.trim() : defaultImageUrl,
                "state":state.trim() ? state.trim() : defaultState,
                "county":county.trim() ? county.trim() : DefaultCounty,
                "latitude":latitude.trim() ? latitude.trim() : DefaultLatitude,
                "longitude":longitude.trim() ? longitude.trim() : DefaultLongitude,
                "facilities":facilitiesArray,
            }
            console.log(parkJson);

            // Using Fetch API
            fetch((updateParkEndpoint + id), { // should be + id not + parkName
                method: 'PUT',
                headers: { 
                    'Content-Type': 'application/json',
                    'Access-Control-Allow-Origin':  'https://capstone3parksapp.azurewebsites.net',
                    'Access-Control-Allow-Methods': 'PUT',
                },
                body: JSON.stringify(parkJson),
                credentials: 'include'
            })
            .then((response) => { 
                if (!response.ok) {throw new Error(String(response.status)); }
                else {return response.json(); } })
            .then((data) => {
                // successfully posted! clear fields and alert user
                alert("Congratulations the park has been successfully added!");
            })
            .catch((err) => {
                alert("Unable to add park to the database")
            });

        } else {
            alert("A New Park must have a Name and an Address. Please enter values and try again.")
        }
    }

    // amenities is an array of facilities included via their string name
    const [amenitiesValue, setAmenitiesValue] = React.useState<string[]>([]);

    const handleChange = (event: SelectChangeEvent<typeof amenitiesValue>) => {
        const {
        target: { value },
        } = event;
        createFacilityArray(
        // On autofill we get a stringified value.
        typeof value === 'string' ? value.split(',') : value,
        );
    };

    // allows each facility to update its values so park, facility, and court info can all be posted together.
    const [facilitiesArray, setFacilitiesArray] = React.useState<any[]>([]);
    const [changesMade, setChangesMade] = React.useState(false);

    const updateFacilityInformation = (facilityInformation: any, index: number) => {
        setChangesMade(true);
        let newFacilitiesArray = facilitiesArray;
        let facilityToUpdate = facilitiesArray[index];
        // update fields individually to keep fields not adjusted:
        // currently actual parameters of facility that will be editable are: name, address, capacity, description
        facilityToUpdate.name = facilityInformation.name ? facilityInformation.name : amenitiesValue[index];
        facilityToUpdate.address = facilityInformation.address ? facilityInformation.address : address;
        facilityToUpdate.description = facilityInformation.description;
        facilityToUpdate.capacity = facilityInformation.capacity ? facilityInformation.capacity : "0";
        facilityToUpdate.courts = facilityInformation.courts;
        if (facilityInformation.newHours) {
            let startTime = facilityInformation.newHours.split(" to ")[0]
            let endTime = facilityInformation.newHours.split(" to ")[1]
            if (startTime.includes("AM")) {
                startTime = startTime.split(" ")[0]
                const hour = parseInt(startTime.split(":")[0])
                const minute = parseInt(startTime.split(":")[1])
                const hour_str = hour.toString().padStart(2, "0")
                const minute_str = minute.toString().padStart(2, "0")
                startTime = hour_str + ":" + minute_str
            } else {
                startTime = startTime.split(" ")[0]
                const hour = parseInt(startTime.split(":")[0]) + 12
                const minute = parseInt(startTime.split(":")[1])
                const hour_str = hour.toString().padStart(2, "0")
                const minute_str = minute.toString().padStart(2, "0")
                startTime = hour_str + ":" + minute_str
            }
            if (endTime.includes("AM")) {
                endTime = endTime.split(" ")[0]
                const hour = parseInt(endTime.split(":")[0])
                const minute = parseInt(endTime.split(":")[1])
                const hour_str = hour.toString().padStart(2, "0")
                const minute_str = minute.toString().padStart(2, "0")
                endTime = hour_str + ":" + minute_str
            } else {
                endTime = endTime.split(" ")[0]
                const hour = parseInt(endTime.split(":")[0]) + 12
                const minute = parseInt(endTime.split(":")[1])
                const hour_str = hour.toString().padStart(2, "0")
                const minute_str = minute.toString().padStart(2, "0")
                endTime = hour_str + ":" + minute_str
            }
            facilityInformation.newHours = startTime + "-" + endTime
            if (facilityInformation.day === "Monday") {
                facilityToUpdate.monday_hours = facilityInformation.newHours
            } else if (facilityInformation.day === "Tuesday") {
                facilityToUpdate.tuesday_hours = facilityInformation.newHours
            } else if (facilityInformation.day === "Wednesday") {
                facilityToUpdate.wednesday_hours = facilityInformation.newHours
            } else if (facilityInformation.day === "Thursday") {
                facilityToUpdate.thursday_hours = facilityInformation.newHours
            } else if (facilityInformation.day === "Friday") {
                facilityToUpdate.friday_hours = facilityInformation.newHours
            } else if (facilityInformation.day === "Saturday") {
                facilityToUpdate.saturday_hours = facilityInformation.newHours
            } else if (facilityInformation.day === "Sunday") {
                facilityToUpdate.sunday_hours = facilityInformation.newHours
            } else if (facilityInformation.day === "All") {
                facilityToUpdate.monday_hours = facilityInformation.newHours
                facilityToUpdate.tuesday_hours = facilityInformation.newHours
                facilityToUpdate.wednesday_hours = facilityInformation.newHours
                facilityToUpdate.thursday_hours = facilityInformation.newHours
                facilityToUpdate.friday_hours = facilityInformation.newHours
                facilityToUpdate.saturday_hours = facilityInformation.newHours
                facilityToUpdate.sunday_hours = facilityInformation.newHours
            }
        }
        newFacilitiesArray[index] = facilityToUpdate;
        setFacilitiesArray(newFacilitiesArray);
        
    }

    const createFacilityArray = (amenitiesArray: string[]) => {
        setAmenitiesValue(amenitiesArray);
        let newFacilitiesArray = [];
        for (let i = 0; i < amenitiesArray.length; i++) {
            // make a spot for each facility in facilitiesArray
            newFacilitiesArray.push({
                /*
                Facilites require the following information:

                name VARCHAR(128) NOT NULL,
                state VARCHAR(32),
                county VARCHAR(64),
                address VARCHAR(512),
                latitude VARCHAR(16),
                longitude VARCHAR(16),
                description VARCHAR(512),
                phone_nr VARCHAR(32),
                type VARCHAR(64) NOT NULL,
                capacity INT,
                image_url VARCHAR(256),
                */
                name:amenitiesArray[i], 
                state:state,
                county:county,
                address:address,
                latitude:latitude,
                longitude:longitude,
                description:"",
                phone_nr:phone_nr,
                type:amenitiesArray[i],
                capacity:"0",
                image_url:image_url,
                courts:[{name:"location 1", reservable:true}],
                monday_hours:"08:00-20:00",
                tuesday_hours:"08:00-20:00",
                wednesday_hours:"08:00-20:00",
                thursday_hours:"08:00-20:00",
                friday_hours:"08:00-20:00",
                saturday_hours:"08:00-20:00",
                sunday_hours:"08:00-20:00",
            });
        }
        setFacilitiesArray(newFacilitiesArray);

    }

    return (
        <div>
            <h1>Update Park</h1>

            <div className="FormsAndPreview" style={{
                display:'flex',
                justifyContent:'space-around',
                flexWrap:'wrap',
                padding:20
            }}>
                <Paper elevation={8} style={{width: 500, margin:10, display:'flex', alignItems:'center'}} >
                    <div className="AllForms" style={{
                        display:'flex',
                        flexDirection:'column',
                        justifyContent:'center',
                        alignContent: 'center'
                    }}>
                        <h2>Park Name and Address Required</h2>
                        <div className="NameAndAddress" style={{
                            display:'flex', 
                            justifyContent: 'space-around', 
                            alignContent: 'space-around',
                            padding:10
                        }}>
                            <TextField 
                                id="outlined-basic" 
                                label="Park Name *" 
                                variant="outlined" 
                                value={parkName}
                                inputProps={{ maxLength: 30 }}
                                // color="success" could have focused text field a color other than blue
                                onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                                    setParkName(event.target.value)} }
                            />
                            <div style={{width: 20}}></div>
                            <TextField 
                                id="outlined-basic" 
                                label="Address *" 
                                variant="outlined" 
                                value={address}
                                inputProps={{ maxLength: 40 }}
                                onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                                    setAddress(event.target.value)} }
                            />
                        </div>
                        <div className="StateAndCounty" style={{
                            display:'flex',  
                            justifyContent: 'space-around', 
                            alignContent: 'space-around',
                            padding:10
                        }}>
                            <TextField 
                                id="outlined-basic" 
                                label="State" 
                                variant="outlined" 
                                value={state}
                                onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                                    setState(event.target.value)} }
                            />
                            <div style={{width: 20}}></div>
                            <TextField 
                                id="outlined-basic" 
                                label="County" 
                                variant="outlined" 
                                value={county}
                                inputProps={{ maxLength: 25 }}
                                onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                                    setCounty(event.target.value)} }
                            />
                        </div>
                        <div className="PhoneNumberAndImage" style={{
                            display:'flex',  
                            justifyContent: 'space-around', 
                            alignContent: 'space-around',
                            padding:10
                        }}>
                            <TextField 
                                id="outlined-basic" 
                                label="Phone Number" 
                                variant="outlined" 
                                value={phone_nr}
                                inputProps={{ maxLength: 25 }}
                                onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                                    setPhoneNumber(event.target.value)} }
                            />
                            <div style={{width: 20}}></div>
                            <TextField 
                                id="outlined-basic" 
                                label="Link to Park Image" 
                                variant="outlined" 
                                value={image_url}
                                onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                                    setParkImageURL(event.target.value)} }
                            />
                        </div>
                        <div className="LatitudeAndLongitude" style={{
                            display:'flex',  
                            justifyContent: 'space-around', 
                            alignContent: 'space-around',
                            padding:10
                        }}>
                            <TextField 
                                // style={{backgroundColor:'white'}}
                                id="outlined-basic" 
                                label="Latitude" 
                                variant="outlined" 
                                value={latitude}
                                type="number"
                                onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                                    setLatitude(event.target.value.replace(/[^0-9]/g, ''))} }
                            />
                            <div style={{width: 20}}></div>
                            <TextField 
                                id="outlined-basic" 
                                label="Longitude" 
                                variant="outlined" 
                                value={longitude}
                                type="number"
                                onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                                    setLongitude(event.target.value)} }
                            />
                        </div>
                        <div className="Description" style={{
                            display:'flex',  
                            justifyContent: 'space-around', 
                            alignContent: 'space-around',
                            padding:10
                        }}>
                            <TextField 
                                sx={{width: 465}}
                                id="outlined-basic" 
                                label="Park Description" 
                                variant="outlined" 
                                value={description}
                                onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                                    setDescription(event.target.value)} }
                            />
                        </div>
                        
                        <div className="Facilities">
                        <FormControl sx={{ m: 1, width: 465 }}  >
                            <InputLabel id="multiple-checkbox-label">Facilities</InputLabel>
                            <Select 
                            style={{fontSize:25}}
                            labelId="multiple-checkbox-label"
                            id="multiple-checkbox"
                            multiple
                            value={amenitiesValue}
                            onChange={handleChange}
                            input={<OutlinedInput label="Facilities" />}
                            renderValue={(selected) => textArrayToSymbolArray(selected)}
                            //MenuProps={MenuProps}
                            >
                            {allFacilities.map((facility) => (
                                <MenuItem key={facility} value={facility}>
                                <Checkbox checked={amenitiesValue.indexOf(facility) > -1} />
                                <ListItemText primary={facility} />
                                </MenuItem>
                            ))}
                            </Select>
                        </FormControl>
                        </div>
                    </div>
                </Paper>
                <Paper elevation={8} style={{width: 500, margin:10}}  >
                    <div className="PreviewTextAndCard">
                        <div style={{display:'flex',
                                    flexDirection:'column',
                                    justifyContent: 'center',
                                    alignItems: 'center',
                                    alignContent:'space-around'
                                }}
                        >
                            <h2 style={{width: 350}} >Preview how your park will appear to users:</h2>
                            <ParkCard notClickable={true} park={{
                                "name": parkName,
                                "description": description.trim() ? description.trim() : defaultDescription,
                                "address": address,
                                "amenities": amenitiesValue ? amenitiesValue.join(',') : defaultAmenities,
                                "phone_nr": phone_nr.trim() ? phone_nr.trim() : defaultPhoneNumber,
                                "capacity": capacity.trim() ? capacity.trim() : defaultCapacity,
                                "image_url": image_url.trim() ? image_url.trim() : defaultImageUrl
                            }}/>
                            <div style={{padding:20}}>
                                {
                                    /* Only logged in users (later with higher priveledges can add a park) */
                                    userIsLoggedIn() ? 
                                    <Button 
                                        onClick={() => validateAndSend()} variant="contained" disabled={!(parkName.trim()) || !(address.trim())}
                                    > 
                                        Update Park
                                    </Button>
                                    :
                                    <h1 style={{color:'red'}}>
                                        Log In To Create A Park
                                    </h1>
                                }
                            </div>
                        </div>

                    </div>
                </Paper>
            </div> {/* END OF FORMS AND PREVIEW */}
                {
                    amenitiesValue[0] ? 
                    
                    <div className="Add Facilities" style={{display:'flex', flexDirection:'column', alignItems:'center'}}>
                        <h1>Please provide more information about your park's facilities:</h1>
                        {/* <p>{amenitiesValue.join(', ')}</p> */}
                        {
                            amenitiesValue.map((amenityString, index) => (
                                <AddFacility 
                                    existingFacility={changesMade ? false : facilities[index]}
                                    key={amenityString} 
                                    address={address} 
                                    facility={amenityString} 
                                    updateFacilityInformation={updateFacilityInformation}
                                    index={index}
                                />
                            ))
                        }
                    </div>
                    :
                    <div></div>
                }

        </div>
    )

}

export default UpdatePark;