// JSX to display adding one facility. These will be listed cascadingly when a user
// creates a new park to specify details of each facility/facility of the park

import React, { useEffect } from "react";
import { textArrayToSymbolArray } from "./FacilityIcons";
import { Button, MenuItem, Paper, Select, TextField } from "@mui/material";
import AddCourts from "./AddCourts";

const AddFacility = (props: any ): JSX.Element => {

    //console.log(props.existingFacility);

    // State variables for facility values
    const [name, setName] = React.useState(props.existingFacility? props.existingFacility.name : props.facility);
    const [address, setAddress] = React.useState(props.existingFacility? props.existingFacility.address : props.address);
    const [description, setDescription] = React.useState(props.existingFacility? props.existingFacility.description : "");
    const [capacity, setCapacity] = React.useState(props.existingFacility? props.existingFacility.capacity : "0");
    const [numberOfCourts, setNumberOfCourts] = React.useState(props.existingFacility? props.existingFacility.courts.length : "");
    const [courtsArray, setCourtsArray] = React.useState<any[]>(props.existingFacility? props.existingFacility.courts : [{name:"location 1", reservable:true}]);

    // use these setters, so that values are propogated up to parent
    const setFacilityName = (newName: string) => {
        setName(newName);
        props.updateFacilityInformation({
            name:newName, 
            address:address, 
            description:description, 
            capacity:capacity,
            courts:courtsArray,
        }, props.index);
    }

    const setFacilityAddress = (newAddress: string) => {
        setAddress(newAddress);
        props.updateFacilityInformation({
            name:name, 
            address:newAddress, 
            description:description, 
            capacity:capacity,
            courts:courtsArray,
        }, props.index);
    }

    const setFacilityDescription = (newDescription: string) => {
        setDescription(newDescription);
        props.updateFacilityInformation({
            name:name, 
            address:address, 
            description:newDescription, 
            capacity:capacity,
            courts:courtsArray,
        }, props.index);
    }

    const setFacilityCapacity = (newCapacity: string) => {
        setCapacity(newCapacity);
        props.updateFacilityInformation({
            name:name, 
            address:address, 
            description:description, 
            capacity:newCapacity,
            courts:courtsArray,
        }, props.index);
    }
    
    // For storing the completed facility's hours
    const [showHours, setShowHours] = React.useState(false);
    /*
    const [mondayHours, setMondayHours] = React.useState("");
    const [tuesdayHours, setTuesdayHours] = React.useState("");
    const [wednesdayHours, setWednesdayHours] = React.useState("");
    const [thursdayHours, setThursdayHours] = React.useState("");
    const [fridayHours, setFridayHours] = React.useState("");
    const [saturdayHours, setSaturdayHours] = React.useState("");
    const [sundayHours, setSundayHours] = React.useState("");
    */

    const toAnalog = (time: string): string => {
        let hours = parseInt(time.split(":")[0]);
        let suffix = "";
        if (hours > 12) {
            hours -= 12;
            suffix = ":00 PM";
        } else {
            suffix = ":00 AM";
        }
        return "" + hours + suffix;
    }


    // For allowing creator to edit the facility's hours
    const [monStart, setMonStart] = React.useState(props.existingFacility ? toAnalog(props.existingFacility.monday_hours.split("-")[0]) : "8:00 AM");
    const [tuesStart, setTuesStart] = React.useState(props.existingFacility ? toAnalog(props.existingFacility.tuesday_hours.split("-")[0]) : "8:00 AM");
    const [wedStart, setWedStart] = React.useState(props.existingFacility ? toAnalog(props.existingFacility.wednesday_hours.split("-")[0]) : "8:00 AM");
    const [thursStart, setThursStart] = React.useState(props.existingFacility ? toAnalog(props.existingFacility.thursday_hours.split("-")[0]) : "8:00 AM");
    const [friStart, setFriStart] = React.useState(props.existingFacility ? toAnalog(props.existingFacility.friday_hours.split("-")[0]) : "8:00 AM");
    const [satStart, setSatStart] = React.useState(props.existingFacility ? toAnalog(props.existingFacility.saturday_hours.split("-")[0]) : "8:00 AM");
    const [sunStart, setSunStart] = React.useState(props.existingFacility ? toAnalog(props.existingFacility.sunday_hours.split("-")[0]) : "8:00 AM");
    const [monEnd, setMonEnd] = React.useState(props.existingFacility ? toAnalog(props.existingFacility.monday_hours.split("-")[1]) : "8:00 PM");
    const [tuesEnd, setTuesEnd] = React.useState(props.existingFacility ? toAnalog(props.existingFacility.tuesday_hours.split("-")[1]) : "8:00 PM");
    const [wedEnd, setWedEnd] = React.useState(props.existingFacility ? toAnalog(props.existingFacility.wednesday_hours.split("-")[1]) : "8:00 PM");
    const [thursEnd, setThursEnd] = React.useState(props.existingFacility ? toAnalog(props.existingFacility.thursday_hours.split("-")[1]) : "8:00 PM");
    const [friEnd, setFriEnd] = React.useState(props.existingFacility ? toAnalog(props.existingFacility.friday_hours.split("-")[1]) : "8:00 PM");
    const [satEnd, setSatEnd] = React.useState(props.existingFacility ? toAnalog(props.existingFacility.saturday_hours.split("-")[1]) : "8:00 PM");
    const [sunEnd, setSunEnd] = React.useState(props.existingFacility ? toAnalog(props.existingFacility.sunday_hours.split("-")[1]) : "8:00 PM");
    const [allStart, setAllStart] = React.useState("8:00 AM");
    const [allEnd, setAllEnd] = React.useState("8:00 PM");

    const updateHours = (day: string, isStart: boolean, newTime: string) => {
        let newHours = ""
        if (isStart) {
            if (day === "Monday") {
                newHours = newTime + " to " + monEnd;
                setMonStart(newTime);
            } else if (day === "Tuesday") {
                newHours = newTime + " to " + tuesEnd;
                setTuesStart(newTime);
            } else if (day === "Wednesday") {
                newHours = newTime + " to " + wedEnd;
                setWedStart(newTime);
            } else if (day === "Thursday") {
                newHours = newTime + " to " + thursEnd;
                setThursStart(newTime);
            } else if (day === "Friday") {
                newHours = newTime + " to " + friEnd;
                setFriStart(newTime);
            } else if (day === "Saturday") {
                newHours = newTime + " to " + satEnd;
                setSatStart(newTime);
            } else if (day === "Sunday") {
                newHours = newTime + " to " + sunEnd;
                setSunStart(newTime);
            } else if (day === "All") {
                newHours = newTime + " to " + allEnd;
                setMonStart(newTime);
                setTuesStart(newTime);
                setWedStart(newTime);
                setThursStart(newTime);
                setFriStart(newTime);
                setSatStart(newTime);
                setSunStart(newTime);
                setAllStart(newTime);
                setMonEnd(allEnd);
                setTuesEnd(allEnd);
                setWedEnd(allEnd);
                setThursEnd(allEnd);
                setFriEnd(allEnd);
                setSatEnd(allEnd);
                setSunEnd(allEnd);
            }
        } else  {
            // is end
            if (day === "Monday") {
                newHours = monStart + " to " + newTime;
                setMonEnd(newTime);
            } else if (day === "Tuesday") {
                newHours = tuesStart + " to " + newTime;
                setTuesEnd(newTime);
            } else if (day === "Wednesday") {
                newHours = monStart + " to " + newTime;
                setWedEnd(newTime);
            } else if (day === "Thursday") {
                newHours = thursStart + " to " + newTime;
                setThursEnd(newTime);
            } else if (day === "Friday") {
                newHours = friStart + " to " + newTime;
                setFriEnd(newTime);
            } else if (day === "Saturday") {
                newHours = satStart + " to " + newTime;
                setSatEnd(newTime);
            } else if (day === "Sunday") {
                newHours = sunStart + " to " + newTime;
                setSunEnd(newTime);
            } else if (day === "All") {
                newHours = allStart + " to " + newTime;
                setMonStart(allStart);
                setTuesStart(allStart);
                setWedStart(allStart);
                setThursStart(allStart);
                setFriStart(allStart);
                setSatStart(allStart);
                setSunStart(allStart);
                setAllEnd(newTime);
                setMonEnd(newTime);
                setTuesEnd(newTime);
                setWedEnd(newTime);
                setThursEnd(newTime);
                setFriEnd(newTime);
                setSatEnd(newTime);
                setSunEnd(newTime);
            }
        }

        props.updateFacilityInformation({
            name:name, 
            address:address, 
            description:description, 
            capacity:capacity,
            courts:courtsArray,
            newHours:newHours,
            day:day,
        }, props.index);
    }



    // For allowing creator to edit the court names and reservability
    const [showCourtsModal, setShowCourtsModal] = React.useState(false);

    // props.facility contains the string name of the facility. set the icon and certain words like
    // court or field accordingly

    // call 'courts' different things dependng on the sport
    const getPhraseForCourts = (): String => {
        if (props.facility === "Baseball" || props.facility === "Soccer" || props.facility === "Football" || props.facility === "Dog Park") {
            return "fields";
        } else if (props.facility === "Frisbee Disc Golf" || props.facility === "Golf") {
            return "courses";
        } else if (props.facility === "Skate Park") {
            return "skate parks"
        } else {
            return "courts";
        }
    }

    // Create an array of courts to be mapped into jsx for the number of courts chosen
    const createCourtsArray = (numString: string) => {
        let num = parseInt(numString);
        if (!numString) {
            num = 1;
        }
        setNumberOfCourts(numString);
        let newCourtsArray = [];
        for (let i = 0; i < num; i++) {
            // make a spot for each court in courtsArray
            newCourtsArray.push({name:getPhraseForCourts().substring(0, getPhraseForCourts().length - 1) + " " + (i + 1), reservable:true});
        }
        setCourtsArray(newCourtsArray);
        props.updateFacilityInformation({
            name:name, 
            address:address, 
            description:description, 
            capacity:capacity,
            courts:newCourtsArray,
        }, props.index);
    }

    // moves from child AddCourts component up here to parent AddFacility component, lets the child
    // change its values at its array in the index.
    const setCourtValues = (newValues: any, index: number) => {
        let newCourtsArray = courtsArray;
        newCourtsArray[index] = newValues;
        setCourtsArray(newCourtsArray);
        props.updateFacilityInformation({
            name:name, 
            address:address, 
            description:description, 
            capacity:capacity,
            courts:newCourtsArray,
        }, props.index);
    }

    // Actual parameters of facility that will be editable are: name, address, capacity, description ?
    const times = [
        "12:00 AM",
        "1:00 AM",
        "2:00 AM",
        "3:00 AM",
        "4:00 AM",
        "5:00 AM",
        "6:00 AM",
        "7:00 AM",
        "8:00 AM",
        "9:00 AM",
        "10:00 AM",
        "11:00 AM",
        "12:00 PM",
        "1:00 PM",
        "2:00 PM",
        "3:00 PM",
        "4:00 PM",
        "5:00 PM",
        "6:00 PM",
        "7:00 PM",
        "8:00 PM",
        "9:00 PM",
        "10:00 PM",
        "11:00 PM",
    ]

    return (
        <div style={{width:"95%"}}>
            {
                showCourtsModal ? 
                <div>
                    <div className="VerticalBoxForCourtsAndButton" style={{
                        backgroundColor: "#edffff",
                        display:'flex',
                        borderRadius:'30px',
                        flexWrap: 'wrap',
                        flexDirection:'column',
                        justifyContent:'space-evenly',
                        alignItems:'center',
                        paddingTop:'18px',
                        paddingBottom:'18px',
                        border: '3px solid black',
                    }}>
                        <div className="CourtsName">
                            <h2>{courtsArray.length} {props.facility} {getPhraseForCourts()}</h2>
                        </div>
                        <div className="HorizontalFacilityBar" style={{
                            display:'flex',
                            flexWrap: 'wrap',
                            justifyContent:'space-evenly',
                            alignItems:'center',
                            paddingTop:'18px',
                            paddingBottom:'18px',
                            
                        }}>
                            {
                                courtsArray.map((courtValues, index) => (
                                    <AddCourts setCourtValues={setCourtValues} index={index} key={index} court={courtValues} />
                                ))
                            }
                        </div>
                        <div className="LeaveCourtsButton">
                            <Button
                                variant='contained'
                                onClick={() => {setShowCourtsModal(false)}}
                            >
                                Back To Facility
                            </Button>
                        </div>
                    </div>
                </div>
                :
                <div>
                    {
                        showHours ?
                        <div className="HorizontalFacilityBar" style={{
                            borderRadius: '30px',
                            backgroundColor: "lightcyan",
                            display:'flex',
                            flexWrap: 'wrap',
                            justifyContent:'space-evenly',
                            alignItems:'center',
                            paddingTop:'18px',
                            paddingBottom:'18px',
                            border: '3px solid  black', // #c7f1c4 -> the light green background color
                        }}>
                            <div className="IconBox" style={{ fontSize:80}}>
                                {textArrayToSymbolArray([props.facility])}
                            </div>

                            <div className="AllMondayTuesdayWednesday" style={{display:'flex', flexDirection:'column', justifyContent:'space-between', alignItems:'flex-end'}}>
                                <div className="allHours" style={{margin:5, display:'flex', alignItems:'center' }}>
                                    <p>All Days From: </p>
                                    <Select 
                                        defaultValue={"8:00 AM"} 
                                        sx={{m: 2}} 
                                        onChange={e => updateHours("All", true, e.target.value)}
                                        label="Start"
                                        value={allStart}
                                    >
                                        {
                                            times.map(time => (
                                                <MenuItem value={time} >{time}</MenuItem>
                                            ))
                                        }
                                    </Select>
                                    <p>To: </p>
                                    <Select 
                                        defaultValue={"8:00 PM"} 
                                        sx={{m: 2}} 
                                        onChange={e => updateHours("All", false, e.target.value)}
                                        label="End"
                                        value={allEnd}
                                    >
                                        {
                                            times.map(time => (
                                                <MenuItem value={time} >{time}</MenuItem>
                                            ))
                                        }
                                    </Select>
                                </div>
                                <div className="mondayHours" style={{margin:5, display:'flex', alignItems:'center' }}>
                                    <p>Monday From: </p>
                                    <Select 
                                        defaultValue={"8:00 AM"} 
                                        sx={{m: 2}} 
                                        onChange={e => updateHours("Monday", true, e.target.value)}
                                        label="Start"
                                        value={monStart}
                                    >
                                        {
                                            times.map(time => (
                                                <MenuItem value={time} >{time}</MenuItem>
                                            ))
                                        }
                                    </Select>
                                    <p>To: </p>
                                    <Select 
                                        defaultValue={"8:00 PM"} 
                                        sx={{m: 2}} 
                                        onChange={e => updateHours("Monday", false, e.target.value)}
                                        label="End"
                                        value={monEnd}
                                    >
                                        {
                                            times.map(time => (
                                                <MenuItem value={time} >{time}</MenuItem>
                                            ))
                                        }
                                    </Select>
                                </div>
                                <div className="tuesdayHours" style={{margin:5, display:'flex', alignItems:'center' }}>
                                    <p>Tuesday From: </p>
                                    <Select 
                                        defaultValue={"8:00 AM"} 
                                        sx={{m: 2}} 
                                        onChange={e => updateHours("Tuesday", true, e.target.value)}
                                        label="Start"
                                        value={tuesStart}
                                    >
                                        {
                                            times.map(time => (
                                                <MenuItem value={time} >{time}</MenuItem>
                                            ))
                                        }
                                    </Select>
                                    <p>To: </p>
                                    <Select 
                                        defaultValue={"8:00 PM"} 
                                        sx={{m: 2}} 
                                        onChange={e => updateHours("Tuesday", false, e.target.value)}
                                        label="End"
                                        value={tuesEnd}
                                    >
                                        {
                                            times.map(time => (
                                                <MenuItem value={time} >{time}</MenuItem>
                                            ))
                                        }
                                    </Select>
                                </div>
                                <div className="wednesDayHours" style={{margin:5, display:'flex', alignItems:'center' }}>
                                    <p>Wednesday From: </p>
                                    <Select 
                                        defaultValue={"8:00 AM"} 
                                        sx={{m: 2}} 
                                        onChange={e => updateHours("Wednesday", true, e.target.value)}
                                        label="Start"
                                        value={wedStart}
                                    >
                                        {
                                            times.map(time => (
                                                <MenuItem value={time} >{time}</MenuItem>
                                            ))
                                        }
                                    </Select>
                                    <p>To: </p>
                                    <Select 
                                        defaultValue={"8:00 PM"} 
                                        sx={{m: 2}} 
                                        onChange={e => updateHours("Wednesday", false, e.target.value)}
                                        label="End"
                                        value={wedEnd}
                                    >
                                        {
                                            times.map(time => (
                                                <MenuItem value={time} >{time}</MenuItem>
                                            ))
                                        }
                                    </Select>
                                </div>
                            </div>
                            <div className="ThursdayFridaySaturdaySunday" style={{display:'flex', flexDirection:'column', justifyContent:'space-between', alignItems:'flex-end'}}>
                                <div className="thursdayHours" style={{margin:5, display:'flex', alignItems:'center' }}>
                                    <p>Thursday From: </p>
                                    <Select 
                                        defaultValue={"8:00 AM"} 
                                        sx={{m: 2}} 
                                        onChange={e => updateHours("Thursday", true, e.target.value)}
                                        label="Start"
                                        value={thursStart}
                                    >
                                        {
                                            times.map(time => (
                                                <MenuItem value={time} >{time}</MenuItem>
                                            ))
                                        }
                                    </Select>
                                    <p>To: </p>
                                    <Select 
                                        defaultValue={"8:00 PM"} 
                                        sx={{m: 2}} 
                                        onChange={e => updateHours("Thursday", false, e.target.value)}
                                        label="End"
                                        value={thursEnd}
                                    >
                                        {
                                            times.map(time => (
                                                <MenuItem value={time} >{time}</MenuItem>
                                            ))
                                        }
                                    </Select>
                                </div>
                                <div className="fridayHours" style={{margin:5, display:'flex', alignItems:'center' }}>
                                    <p>Friday From: </p>
                                    <Select 
                                        defaultValue={"8:00 AM"} 
                                        sx={{m: 2}} 
                                        onChange={e => updateHours("Friday", true, e.target.value)}
                                        label="Start"
                                        value={friStart}
                                    >
                                        {
                                            times.map(time => (
                                                <MenuItem value={time} >{time}</MenuItem>
                                            ))
                                        }
                                    </Select>
                                    <p>To: </p>
                                    <Select 
                                        defaultValue={"8:00 PM"} 
                                        sx={{m: 2}} 
                                        onChange={e => updateHours("Friday", false, e.target.value)}
                                        label="End"
                                        value={friEnd}
                                    >
                                        {
                                            times.map(time => (
                                                <MenuItem value={time} >{time}</MenuItem>
                                            ))
                                        }
                                    </Select>
                                </div>
                                <div className="saturdayHours" style={{margin:5, display:'flex', alignItems:'center' }}>
                                    <p>Saturday From: </p>
                                    <Select 
                                        defaultValue={"8:00 AM"} 
                                        sx={{m: 2}} 
                                        onChange={e => updateHours("Saturday", true, e.target.value)}
                                        label="Start"
                                        value={satStart}
                                    >
                                        {
                                            times.map(time => (
                                                <MenuItem value={time} >{time}</MenuItem>
                                            ))
                                        }
                                    </Select>
                                    <p>To: </p>
                                    <Select 
                                        defaultValue={"8:00 PM"} 
                                        sx={{m: 2}} 
                                        onChange={e => updateHours("Saturday", false, e.target.value)}
                                        label="End"
                                        value={satEnd}
                                    >
                                        {
                                            times.map(time => (
                                                <MenuItem value={time} >{time}</MenuItem>
                                            ))
                                        }
                                    </Select>
                                </div>
                                <div className="sundayHours" style={{margin:5, display:'flex', alignItems:'center' }}>
                                    <p>Sunday From: </p>
                                    <Select 
                                        defaultValue={"8:00 AM"} 
                                        sx={{m: 2}} 
                                        onChange={e => updateHours("Sunday", true, e.target.value)}
                                        label="Start"
                                        value={sunStart}
                                    >
                                        {
                                            times.map(time => (
                                                <MenuItem value={time} >{time}</MenuItem>
                                            ))
                                        }
                                    </Select>
                                    <p>To: </p>
                                    <Select 
                                        defaultValue={"8:00 PM"} 
                                        sx={{m: 2}} 
                                        onChange={e => updateHours("Sunday", false, e.target.value)}
                                        label="End"
                                        value={sunEnd}
                                    >
                                        {
                                            times.map(time => (
                                                <MenuItem value={time} >{time}</MenuItem>
                                            ))
                                        }
                                    </Select>
                                </div>
                            </div>

                            <div className="EditCourtsButtonAndHoursButton" style={{display:'flex', flexDirection:'column', alignItems:'center'}}>
                                <Button
                                    variant='contained'
                                    disabled={!numberOfCourts || parseInt(numberOfCourts) < 1}
                                    onClick={() => {setShowCourtsModal(true)}}
                                    style = {{width:'150px'}}
                                >
                                    Edit {getPhraseForCourts()}
                                </Button>
                                <Button
                                    variant='contained'
                                    onClick={() => {setShowHours(!showHours)}} 
                                    color={showHours ? "secondary" : "primary"}
                                    style = {{width:'150px'}}
                                >
                                    {showHours ? "BACK" : "Set Hours"}
                                </Button>
                            </div>
                        </div>
                        :
                        <Paper elevation={10} style={{borderRadius: '30px', border: '3px solid  black', margin:5}}>
                            <div className="HorizontalFacilityBar" style={{
                                borderRadius: '30px',
                                backgroundColor: "#edf5ff",
                                display:'flex',
                                flexWrap: 'wrap',
                                justifyContent:'space-evenly',
                                alignItems:'center',
                                paddingTop:'18px',
                                paddingBottom:'18px',
                            }}>
                                <div className="IconBox" style={{ fontSize:80}}>
                                    {textArrayToSymbolArray([props.facility])}
                                </div>
                                <div className="facilityName" style={{ }}>
                                <TextField 
                                        id="outlined-basic" 
                                        label="Facility Name" 
                                        variant="outlined" 
                                        value={name}
                                        // color="success" could have focused text field a color other than blue
                                        onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                                            setFacilityName(event.target.value)} }
                                    />
                                </div>
                                <div className="facilityAddress" style={{ }}>
                                <TextField 
                                        id="outlined-basic" 
                                        label="Facility Address" 
                                        variant="outlined" 
                                        value={address}
                                        // color="success" could have focused text field a color other than blue
                                        onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                                            setFacilityAddress(event.target.value)} }
                                    />
                                </div>
                                <div className="facilityDescription" style={{ }}>
                                <TextField 
                                        id="outlined-basic" 
                                        label="Facility Description" 
                                        variant="outlined" 
                                        value={description}
                                        // color="success" could have focused text field a color other than blue
                                        onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                                            setFacilityDescription(event.target.value)} }
                                    />
                                </div>
                                <div className="facilityCapacity" style={{ }}>
                                    <TextField 
                                        id="outlined-basic" 
                                        label="Facility Capacity" 
                                        variant="outlined" 
                                        value={capacity}
                                        // color="success" could have focused text field a color other than blue
                                        onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                                            setFacilityCapacity(event.target.value)} }
                                    />
                                </div>
                                <div className="FacilityCourts" style={{ }}>
                                    <TextField 
                                        id="outlined-basic" 
                                        label={("Number of " + getPhraseForCourts())}
                                        variant="outlined" 
                                        value={numberOfCourts}
                                        // color="success" could have focused text field a color other than blue
                                        onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                                            createCourtsArray(( event.target.value.replace(/[^(\d?\d?)]|\d{3}/g, '') === '' || parseInt(event.target.value.replace(/[^(\d?\d?)]|\d{3}/g, '')) <= 20 ? 
                                            event.target.value.replace(/[^(\d?\d?)]|\d{3}/g, '')
                                            : "20" ));
                                        } }
                                    />
                                </div>
                                <div className="EditCourtsButtonAndHoursButton" style={{display:'flex', flexDirection:'column', alignItems:'center'}}>
                                    <Button
                                        variant='contained'
                                        disabled={!numberOfCourts || parseInt(numberOfCourts) < 1}
                                        onClick={() => {setShowCourtsModal(true)}}
                                        style = {{width:'150px'}}
                                    >
                                        Edit {getPhraseForCourts()}
                                    </Button>
                                    <Button
                                        variant='contained'
                                        onClick={() => {setShowHours(!showHours)}} 
                                        color={showHours ? "secondary" : "primary"}
                                        style = {{width:'150px'}}
                                    >
                                        {showHours ? "BACK" : "Set Hours"}
                                    </Button>
                                </div>
                            </div>
                        </Paper>
                        
                    }
                </div>
            }

            
        </div>
    )
}


export default AddFacility;